@import "../../variables.scss";
@import "../../mixin.scss";

.marketDetail {
    color: $v2_text;
    line-height: 1;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    .detailHeader {
        background: $BgBlack;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 33px;
        .container {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;

            a {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                font-weight: 400;
                font-size: 18px;
                color: $PureWhite;
                text-decoration: none;
                text-transform: capitalize;
                align-self: flex-start;
                >span {
                    margin-left: 12px;
                }
                &:hover {
                    color: $LightenGrey;
                }
            }

            .introContainer {
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 100%;

                > img {
                    width: 72px;
                }
                .marketName {
                    margin-top: 12px;
                    font-size: 20px;
                    line-height: 24px;
                    font-weight: 700;
                    color: $PureWhite;
                }
                .marketSymbol {
                    margin-top: 4px;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 16.8px;
                    color: $LGrey;
                }
            }

            .cards {
                display: flex;
                flex-direction: row;
                margin-top: 33px;
                width:100%;
                .loanLimitContainer {
                    padding: 3em;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    width: 33.333%;
                    background-color: rgba($Green, 0.5);
                    border-radius: 12px;
                    margin: 0 12px;
                    .txtTitle {
                        font-size: 14px;
                        line-height: 24px;
                        font-weight: 400;
                        color: $PureWhite;
                        text-align: center;
                    }

                    .txtNum {
                        margin-top: 4px;
                        font-size: 32px;
                        color: $PureWhite;
                        text-align: center;
                        word-break: break-all;
                    }
                }
            }
        }
    }

    .mainContainer {
        padding-top: 26px;
        padding-bottom: 80px;
        width: 100%;
        display: flex;
        flex-direction: row;
        background-image: linear-gradient(to bottom, $BgBlack 245px, $Bg 0%);
        .container {
            display: flex;
            flex-direction: row;
        }
        .chartContainer {
            width: 50%;
            background: $PureWhite;
            padding: 20px;
            border-radius: 12px;
            color: black;
            margin: 0 12px;
            flex-wrap: wrap;
            .caption {
                font-size: 18px;
                color: $DarkBlack;
                line-height: 21.6px;
                font-weight: 500;
                text-transform: capitalize;
                padding-bottom: 15px;
                border-bottom: 1px solid $LineGrey;
            }

            .chartDescription {
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                justify-content: space-between;
                font-size: 14px;
                margin-top: 20px;
                padding:0 20px;
                .item {
                    .txtTitle {
                        position: relative;
                        color: $DarkenGrey;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 16.8px;
                        padding-left: 18px;
                        &:before {
                            content: '';
                            position: absolute;
                            left:0;
                            top: 50%;
                            transform: translate(0, -50%);
                            width: 10px;
                            height: 10px;
                            border-radius: 50%;
                            background: $purple;
                        }
                    }
                    .txtNum {
                        margin-top: 5px;
                        margin-bottom: 20px;
                        color: $DarkBlack;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 16.8px;
                    }
                }
                .borrowItem {
                    .txtTitle {
                        &:before {
                            background: $Blue;
                        }
                    }
                }
                .supplyItem {
                    .txtTitle {
                        &:before {
                            background: $Green;
                        }
                    }
                }
            }

            .chartDescription + svg {
                margin-top: 30px;
            }

            .list {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                font-size: 16px;
                margin-top: 20px;
                .item {
                    display: flex;
                    flex-wrap: wrap;
                    flex-direction: row;
                    justify-content: space-between;
                    margin: 6px 0;
                    .txtTitle {
                        font-size: 16px;
                        line-height: 19.2px;
                        font-weight: 500;
                        margin: 6px 0;
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-self: center;
                        color: $DarkenGrey;
                    }
                    .txtNum {
                        font-size: 16px;
                        line-height: 19.2px;
                        font-weight: 500;
                        margin: 6px 0;
                        color: $DarkBlack;
                    }
                    .linkTxtNum {
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        a {
                            text-decoration: none;
                            color: $Blue;
                            &:hover {
                                text-decoration: underline;
                            }
                            >svg {
                                margin-left: 4px;
                            }
                        }
                    }
                }
            }
        }
    }
}


:global {
    .marketDetail-tooltips {
        p {
            margin: 0;
            margin-top: 5px;
            padding: 0;
            &:first-child {
                margin-top: 0;
            }
        }
    }

}


@media (max-width: 576px) {
    .marketDetail {
        .detailHeader {
            .container {
                .cards {
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    .loanLimitContainer {
                        width: 100%;
                        margin: 10px 0;
                    }
                }
            }
        }

        .mainContainer {
            .container {
                flex-direction: column;
                .chartContainer {
                    width: 100%;
                    margin: 10px 0;
                    .chartDescription {
                        flex-direction: column;
                        .item {
                            flex-direction: row;
                            justify-content: space-between;
                            margin: 2px 0;
                            .txtTitle {
                                display: initial;
                            }
                            .txtNum {
                                display: initial;
                                margin-left: 10px;
                            }
                        }
                    }
                    .list {
                        .item {
                            margin: 5px 0;
                            .txtTitle,
                            .txtNum {
                                margin: 5px 0;
                            }
                        }
                        .rateItem {
                            .txtTitle,
                            .txtNum {
                                width: 100%;
                            }
                            .txtNum {
                                margin-top: 5px;
                            }
                        }
                    }
                }
            }


        }
    }
}
